/* Documentation site component styles */
.example {
  border: solid 1px #d3d3d3;
  margin-bottom: 16px;
}

.example h4:first-of-type {
  margin-top: 0;
}

.example p:last-of-type {
  margin-bottom: 0;
}

.example--header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 2.6rem;
  line-height: 2.6rem;
  background-color: #ebebeb;
  cursor: pointer;

  /* Overide the <pre> margin */
  margin-bottom: -7px;
}

.example--example {
  padding: 16px;
  background-color: var(--white);
}

.example--name {
  position: relative;
  left: 16px;
}

.example--codelink {
  font-size: 0.8rem;
  position: relative;
  right: 16px;
  color: rgba(0, 0, 0, 0.5);
}

.example--codelink:hover {
  color: rgba(0, 0, 0, 0.8);
}

.example--code {
  overflow: hidden;
  -webkit-transition: max-height 250ms ease-in-out;
  -o-transition: max-height 250ms ease-in-out;
  transition: max-height 250ms ease-in-out;
  max-height: 2000px;
}

.example--code-hide {
  max-height: 0;
}

.example--description-wrap {
  padding: 26px 16px 16px 16px;
}

.example--code-description {
  margin: 0;
  margin-bottom: 1.5rem;
  line-height: 1rem;
  color: rgba(0, 0, 0, 0.5);
}



